  --jp-white:           #FFFFFF;
  --jp-white-off:       #F9F9F6;
  --jp-gray:            #DCDCDE;
  --jp-gray-off:        #E2E2DF;
  --jp-red:             #D63639;
  --jp-green-primary:   #069E08;
  --jp-green-secondary: #2FB41F;
  --jp-border-radius: 4px;
  --jp-menu-border-height: 1px;
  --jp-underline-thickness: 2px; }

