.jp-cut {
  position: relative;
  display: block;
  margin: 32px 0;
  padding: 16px 64px 16px 24px;
  border: 2px solid #069E08;
  border: 2px solid var(--jp-green-primary);
  border-radius: 4px;
  border-radius: var(--jp-border-radius);
  text-decoration: none; }
  .jp-cut span {
    display: block; }
    .jp-cut span:last-of-type {
      font-weight: 600; }
  .jp-cut:hover span:last-of-type, .jp-cut:focus span:last-of-type {
    text-decoration: underline;
    text-decoration-thickness: 2px;
    text-decoration-thickness: var(--jp-underline-thickness); }
  .jp-cut:hover:after, .jp-cut:focus:after {
    transform: translateY(-50%) translateX(8px); }
  .jp-cut:after {
    content: "→";
    position: absolute;
    top: 50%;
    right: 24px;
    font-size: 24px;
    font-weight: 600;
    color: #069E08;
    color: var(--jp-green-primary);
    transform: translateY(-50%);
    transition: transform 0.15s ease-out; }

.jp-disconnect-dialog h1 {
  margin-top: 0;
  line-height: 1.2;
  font-size: 36px;
  font-size: var(--font-title-large);
  font-weight: 600; }

.jp-disconnect-dialog h2 {
  margin: 0;
  line-height: 1.2;
  font-size: 24px;
  font-size: var(--font-title-small);
  font-weight: 400; }

.jp-disconnect-dialog p {
  font-size: 16px;
  font-size: var(--font-body); }

.jp-disconnect-dialog__link {
  font-size: 16px;
  font-size: var(--font-body);
  color: #000000;
  color: var(--jp-black);
  text-decoration: underline;
  height: auto;
  font: inherit;
  padding: 0; }
  .jp-disconnect-dialog__link:hover {
    color: #000000;
    color: var(--jp-black);
    text-decoration-thickness: 2px;
    text-decoration-thickness: var(--jp-underline-thickness); }
  .jp-disconnect-dialog__link:focus {
    color: #000000;
    color: var(--jp-black);
    box-shadow: none !important; }

.jp-disconnect-dialog .components-button {
  height: 40px;
  font-size: var(--font-body-small);
  border-radius: 4px; }

.jp-disconnect-dialog .components-modal__content {
  padding: 0; }

.jp-disconnect-dialog .components-modal__header {
  display: none; }

.jp-disconnect-dialog .jp-row {
  align-items: center; }

.jp-disconnect-dialog__content {
  background: #F9F9F6;
  background: var(--jp-white-off);
  margin: 0;
  padding: 80px;
  border-radius: 4px;
  text-align: center; }

.jp-disconnect-dialog__actions {
  background: #FFFFFF;
  background: var(--jp-white);
  padding: 10px 48px; }

.jp-disconnect-dialog__btn-dismiss {
  background: #000000 !important;
  background: var(--jp-black) !important;
  margin-right: 10px; }

.jp-disconnect-dialog__btn-disconnect {
  background: #D63639 !important;
  background: var(--jp-red) !important; }

.jp-disconnect-dialog__btn-back-to-wp {
  background: #000000 !important;
  background: var(--jp-black) !important; }

.jp-disconnect-dialog__button-wrap {
  text-align: center; }

.jp-disconnect-dialog__error {
  color: #D63639;
  color: var(--jp-red); }

.jp-disconnect-dialog__success .components-modal__content {
  background: #FFFFFF;
  background: var(--jp-white);
  padding: 50px; }

.jp-disconnect-dialog__success h1 {
  margin-top: 20px; }

@media (min-width: 960px) {
  .jp-disconnect-dialog {
    min-width: 900px; }
    .jp-disconnect-dialog__success {
      min-width: auto; }
  .jp-row {
    margin-left: 0px; } }
