.jp-connect-screen {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen-Sans', 'Ubuntu', 'Cantarell', 'Helvetica Neue', sans-serif;
  background: #FFFFFF;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.08);
  border-radius: 4px; }
  .jp-connect-screen.jp-connect-screen--loading {
    display: none; }
  .jp-connect-screen .jp-connect-screen--left, .jp-connect-screen .jp-connect-screen--right {
    box-sizing: border-box; }
  .jp-connect-screen .jp-connect-screen--left {
    padding: 64px 96px; }
    .jp-connect-screen .jp-connect-screen--left h2 {
      font-style: normal;
      font-weight: bold;
      font-size: 36px;
      line-height: 40px;
      color: #000; }
    .jp-connect-screen .jp-connect-screen--left p, .jp-connect-screen .jp-connect-screen--left li {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px; }
    .jp-connect-screen .jp-connect-screen--left p {
      color: #101517; }
    .jp-connect-screen .jp-connect-screen--left ul {
      list-style-type: none; }
      .jp-connect-screen .jp-connect-screen--left ul li {
        background: url(data:image/png;base64,) no-repeat;
        padding-left: 30px;
        color: #000;
        margin-bottom: 9px; }
    .jp-connect-screen .jp-connect-screen--left .jp-connect-button {
      margin-top: 40px; }
    .jp-connect-screen .jp-connect-screen--left .jp-connnect-screen--tos {
      margin-top: 32px;
      max-width: 360px; }
  .jp-connect-screen .jp-connect-screen--right {
    padding: 64px 0; }
    .jp-connect-screen .jp-connect-screen--right img {
      max-width: 100%; }
  .jp-connect-screen.jp-connect-screen--two-columns .jp-connect-screen--left {
    float: left;
    width: 50%; }
  .jp-connect-screen.jp-connect-screen--two-columns .jp-connect-screen--right {
    float: right;
    width: 49%;
    background: #F9F9F6; }
  .jp-connect-screen .jp-connect-screen--clearfix {
    clear: both; }
